  flex-wrap: wrap;
}
.flex > * {
  flex-grow: 1;
}
input,
select,
textarea,
input.form-control,
select.form-control,
textarea.form-control {
  border-radius: 4px;
  border: 2px solid #d3d3d3;
  color: #333;
}
input:focus,
select:focus,
textarea:focus,
input.form-control:focus,
select.form-control:focus,
textarea.form-control:focus {
  border: 2px solid #ffa500;
}
input[disabled],
select[disabled],
textarea[disabled],
input.form-control[disabled],
select.form-control[disabled],
textarea.form-control[disabled] {
  border: 2px solid #a9a9a9;
}
input.is-error,
select.is-error,
textarea.is-error {
  border: 2px solid #de4040;
  color: #333;
}
input.is-valid,
select.is-valid,
textarea.is-valid {
  border: 2px solid;
  color: #333;
}
input[type="file"] {
  border: 0;
}
select,
select.form-control {
/* styling */
  display: inline-block;
  line-height: 1.5em;
  padding: 0.5em 3.5em 0.5em 1em;
/* reset */
  margin: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none;
}
select,
select.form-control {
  background-image: url("http://www.pngmart.com/files/3/Down-Arrow-PNG-Clipart.png");
  background-position: calc(100% - 20px) 1em;
  background-size: 15px;
  background-repeat: no-repeat;
}
.btn {
  border-radius: 3px;
  padding: 10px 40px;
  color: #fff;
  background-color: #06c47a;
  transition: color 1s, box-shadow 1s;
}
.btn:hover {
  background-color: #06c47a;
  color: #fff;
  box-shadow: 2px 0px 8px #9e9c9c;
}
.btn:focus {
  background-color: #06c47a;
  color: #fff;
}
.btn:active {
  background-color: #06c47a;
  color: #fff;
}
.btn-danger,
.btn-success,
.btn-warning {
  border-radius: 3px;
  padding: 10px 40px;
  color: #fff;
}
.btn-danger {
  background-color: #c10500;
}
.btn-danger:hover {
  background-color: #cc0500;
}
.btn-success {
  background-color: #5cb85c;
}
.btn-success:hover {
  background-color: #3e8e3e;
}
.btn-warning {
  background-color: #f0ad4e;
}
.btn-warning:hover {
  background-color: #bc7510;
}
